/* 最大宽度 */
@window-Width: 70%;

.page {
  padding: 32px;
  text-align: center;
}

/* 头部 */
.sidebar-header {
  display: flex;
  justify-content: space-between;
  padding: 16px 32px;
  align-items: center;
  border-bottom: 1px solid #f8f8f8;
}

.sidebar-header .icon {
  color: #2f8cf0;
}

.sidebar-header-title {
  color: #515a6e;
  font-size: 24px;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.sidebar-header-more {
  font-size: 18px;
  color: #989898;
  cursor: pointer;

  &::after {
    content: "";
    display: inline-block;
    width: 12px;
    height: 12px;
    border-top: 1px solid #D9D9D9;
    border-right: 1px solid #D9D9D9;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }

  &:hover {
    color: #2d8cf0;
    text-decoration: underline;
  }
}

.v-note-wrapper {
  box-shadow: none !important;
}

.v-note-wrapper .v-note-panel {
  box-shadow: none !important;
}

.v-note-wrapper .v-note-panel .v-note-show .v-show-content, .v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  background: #fff !important;
}

.v-note-wrapper .v-note-panel .v-note-edit.divarea-wrapper .content-input-wrapper {
  padding: 0 !important;
}

.v-note-wrapper .v-note-panel .v-note-show .v-show-content, .v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  padding: 0 !important;
}







































































































.chapter-detail {
  position: relative;
  width: @window-Width;
  overflow: hidden;
  margin: 24px;
  left: 20%;
  height: 100%;
  border-radius: 6px;
  box-sizing: border-box;
  padding: 32px;
  background: #fff;
}

.chapter-table {
  position: fixed;
  height: 100%;
  width: 20%;
  top: 120px;
  left: 0;
  overflow: hidden;
  overflow-y: auto;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 0 6px 6px 0;
  background: #fff;
}

.chapter-table-title {
  color: #17233d;
  font-size: 24px;
  padding-bottom: 24px;
  font-weight: 500;
  border-bottom: 1px solid #f0f0f0;
}

.chapter-container {
  flex: 1;
  background: #fff;
}

.chapter-box {
  width: 350px;
  margin-right: 32px;
}

.chapter-icon {
  position: fixed;
  left: 0;
  top: 120px;
  background: transparent;
}

.chapter-title {
  color: #17233d;
  text-align: center;
  font-size: 40px;
  font-weight: 500;
  margin-top: 16px;
}

.chapter-intro {
  color: #515a6e;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 32px 0;
}

.chapter-intro li {
  display: inline-block;
  margin-right: 24px;
  font-size: 16px;
  color: #9ea7b4;
  white-space: nowrap;
}

.comment-header {
  font-size: 28px;
  font-weight: 500;
  color: #2d8cf0;
  padding: 32px 0 16px;
  display: flex;
  align-items: center;
}

@media screen and (min-width: 200px) and (max-width: 750px) {
  .chapter-detail {
    position: relative;
    width: 100%;
    overflow: hidden;
    margin: 24px 0;
    left: 0;
    height: 100%;
    border-radius: 6px;
    box-sizing: border-box;
    padding: 32px;
    background: #fff;
  }

  .chapter-table {
    display: none;
    position: fixed;
    height: 100%;
    width: 20%;
    top: 120px;
    left: 0;
    overflow: hidden;
    overflow-y: auto;
    box-sizing: border-box;
    padding: 24px;
    border-radius: 0 6px 6px 0;
    background: #fff;
  }
}
